"use client";

import React, { useState } from "react";
import dynamic from "next/dynamic";
import PageBreadcrumb from "@/components/common/PageBreadCrumb";
import { Modal } from "@/components/ui/modal";
import "react-quill/dist/quill.snow.css";

/* ================= REACT QUILL (NO SSR) ================= */

const ReactQuill = dynamic(() => import("react-quill"), {
    ssr: false,
});

/* ================= TYPES ================= */

interface Policy {
    id: string;
    title: string;
    content: string; // HTML
}

interface Category {
    id: string;
    name: string;
    description: string;
    policies: Policy[];
}

/* ================= DATA ================= */

const initialCategories: Category[] = [
    {
        id: "leave",
        name: "Leave & Time Off",
        description: "Leave and holiday policies",
        policies: [],
    },
    {
        id: "conduct",
        name: "Code of Conduct",
        description: "Workplace behaviour rules",
        policies: [],
    },
];

/* ================= PAGE ================= */

export default function HrPoliciesPage() {
    const [categories, setCategories] = useState<Category[]>(initialCategories);
    const [selectedCategory, setSelectedCategory] = useState<Category | null>(null);
    const [selectedPolicy, setSelectedPolicy] = useState<Policy | null>(null);

    const [showEditor, setShowEditor] = useState(false);
    const [editPolicy, setEditPolicy] = useState<Policy | null>(null);

    const [title, setTitle] = useState("");
    const [content, setContent] = useState("");

    /* ================= ACTIONS ================= */

    const openCreate = () => {
        setEditPolicy(null);
        setTitle("");
        setContent("");
        setShowEditor(true);
    };

    const openEdit = (policy: Policy) => {
        setEditPolicy(policy);
        setTitle(policy.title);
        setContent(policy.content);
        setShowEditor(true);
    };

    const savePolicy = () => {
        if (!selectedCategory || !title) return;

        setCategories((prev) =>
            prev.map((cat) =>
                cat.id === selectedCategory.id
                    ? {
                        ...cat,
                        policies: editPolicy
                            ? cat.policies.map((p) =>
                                p.id === editPolicy.id
                                    ? { ...p, title, content }
                                    : p
                            )
                            : [
                                ...cat.policies,
                                {
                                    id: Date.now().toString(),
                                    title,
                                    content,
                                },
                            ],
                    }
                    : cat
            )
        );

        setShowEditor(false);
    };

    const deletePolicy = (policyId: string) => {
        if (!selectedCategory) return;

        setCategories((prev) =>
            prev.map((cat) =>
                cat.id === selectedCategory.id
                    ? {
                        ...cat,
                        policies: cat.policies.filter((p) => p.id !== policyId),
                    }
                    : cat
            )
        );

        setSelectedPolicy(null);
    };

    /* ================= UI ================= */

    return (
        <div>
            <PageBreadcrumb pageTitle="HR Policies" />

            <div className="rounded-2xl border bg-white p-6">

                {/* CREATE BUTTON (ALWAYS AT TOP) */}
                {selectedCategory && (
                    <div className="mb-6 flex justify-end">
                        <button
                            onClick={openCreate}
                            className="bg-blue-600 text-white px-5 py-2 rounded"
                        >
                            + Create Policy
                        </button>
                    </div>
                )}

                {/* ================= CATEGORIES ================= */}
                {!selectedCategory && (
                    <>
                        <h2 className="text-xl font-semibold mb-6">
                            Policy Categories
                        </h2>

                        <div className="grid grid-cols-1 md:grid-cols-3 gap-6">
                            {categories.map((cat) => (
                                <div
                                    key={cat.id}
                                    onClick={() => setSelectedCategory(cat)}
                                    className="cursor-pointer border rounded-xl p-6 bg-gray-50 hover:shadow"
                                >
                                    <h3 className="font-bold text-lg">{cat.name}</h3>
                                    <p className="text-sm text-gray-500">{cat.description}</p>
                                </div>
                            ))}
                        </div>
                    </>
                )}

                {/* ================= POLICIES ================= */}
                {selectedCategory && !selectedPolicy && (
                    <>
                        <button
                            onClick={() => setSelectedCategory(null)}
                            className="mb-4 text-blue-600"
                        >
                            ‚Üê Back to Categories
                        </button>

                        <h2 className="text-xl font-semibold mb-6">
                            {selectedCategory.name}
                        </h2>

                        <div className="grid grid-cols-1 md:grid-cols-3 gap-6">
                            {selectedCategory.policies.map((policy) => (
                                <div
                                    key={policy.id}
                                    onClick={() => setSelectedPolicy(policy)}
                                    className="cursor-pointer border rounded-xl p-6 bg-gray-50 hover:shadow"
                                >
                                    <h3 className="font-bold">{policy.title}</h3>
                                    <p className="text-sm text-gray-500">
                                        Click to view
                                    </p>
                                </div>
                            ))}

                            {selectedCategory.policies.length === 0 && (
                                <p className="text-gray-400">No policies yet</p>
                            )}
                        </div>
                    </>
                )}
            </div>

            {/* ================= POLICY VIEW ================= */}
            <Modal
                isOpen={!!selectedPolicy}
                onClose={() => setSelectedPolicy(null)}
                className="max-w-2xl p-6"
            >
                {selectedPolicy && (
                    <>
                        <h2 className="text-2xl font-bold mb-4">
                            {selectedPolicy.title}
                        </h2>

                        <div
                            className="prose max-w-none"
                            dangerouslySetInnerHTML={{
                                __html: selectedPolicy.content,
                            }}
                        />

                        <div className="mt-6 flex justify-end gap-3">
                            <button
                                onClick={() => openEdit(selectedPolicy)}
                                className="bg-yellow-500 text-white px-4 py-2 rounded"
                            >
                                Edit
                            </button>
                            <button
                                onClick={() => deletePolicy(selectedPolicy.id)}
                                className="bg-red-600 text-white px-4 py-2 rounded"
                            >
                                Delete
                            </button>
                        </div>
                    </>
                )}
            </Modal>

            {/* ================= CREATE / EDIT MODAL ================= */}
            <Modal
                isOpen={showEditor}
                onClose={() => setShowEditor(false)}
                className="max-w-3xl p-6"
            >
                <h2 className="text-xl font-bold mb-4">
                    {editPolicy ? "Edit Policy" : "Create Policy"}
                </h2>

                <input
                    value={title}
                    onChange={(e) => setTitle(e.target.value)}
                    placeholder="Policy Title"
                    className="w-full border rounded px-4 py-2 mb-4"
                />

                <ReactQuill
                    theme="snow"
                    value={content}
                    onChange={setContent}
                    className="mb-6"
                />

                <div className="flex justify-end gap-3">
                    <button
                        onClick={() => setShowEditor(false)}
                        className="px-4 py-2 bg-gray-500 text-white rounded"
                    >
                        Cancel
                    </button>
                    <button
                        onClick={savePolicy}
                        className="px-4 py-2 bg-blue-600 text-white rounded"
                    >
                        Save
                    </button>
                </div>
            </Modal>
        </div>
    );
}
